             <img class="vsv-social-icns fb" src="~assets/icns/social/fb_icn.svg" alt="fb" width="20px"/>
          </li>
          <li>
            <img class="vsv-social-icns" src="~assets/icns/social/tw_icn.svg" alt="tw" width="20px"/>
          </li>
          <li>
           <img class="vsv-social-icns" src="~assets/icns/social/in_icn.svg" alt="in" width="20px"/>
          </li>
        </ul>
      </div>
    </div>
</template>
<script>
import vsvModal from 'components/ModalLogin';

export default {
  name: 'vsvNavbar',
  data() {
    return {
      showModal: false,
