<template>
  <div>
    <div class="vsv-header">
      <div class="premium-1">
      </div>
      <div class="premium-2">
      </div>
      <div class="premium-3">
      </div>
      <div class=" premium-4">
      </div>
    </div>
  </div>
</template>
<script>
import api from './../data/api/index'

export default {
  name: 'vsvHeader',
  data() {
    return {
      element: 'vsv-header',
      premiumData: false,
      restaurants: []
    };
  },
  created() {
    this.getPremium();
  },
  watch: {
    $route: 'fetchData'
  },
  methods: {
    getPremium() {
      if (this.premiumData) return
        api.getPremium().then((data) => {
          this.restaurants = data
          this.premiumData = true
          // console.log(this.restaurants[0])
        }, (err) => { console.warn(err.body) })
      }
  },
};
</script>
<style lang="scss" scoped>
@import "./../assets/css/main";
    .vsv-header {
        width:100%;
        height: 240px;
        background: $vsv-white;
        display: flex;
    }
    div[class*="premium-"]{
      flex:1;
    }
    .premium-1 {
      background-image: url(~assets/img/premium_rest/1.jpg);
      background-size: cover;
    }
    .premium-2 {
      background-image: url(~assets/img/premium_rest/2.jpg);
      background-size: cover;
    }
    .premium-3 {
      background-image: url(~assets/img/premium_rest/3.jpg);
      background-size: cover;
    }
      .premium-4 {
      background-image: url(~assets/img/premium_rest/4.jpg);
      background-size: cover;
    }

</style>




